import React from 'react'
import Icon from './Icon'

export default function OptionsView() {
    console.log('render MainDisplay')

    function handleClick() {
        navigator.vibrate([100])
    }
    return (
        <article className='vstack px-3 py-3'>
            <section className='alert alert-primary'>
                <h6>Version: 0.3</h6>
            </section>
            <hr/>
            <section>
                <h1>Tests</h1>
                <br/>
                <button className="btn btn-primary icon-link icon-link-hover" onClick={handleClick}>
                    navigator.vibrate
                    <Icon i={'bi-box-seam'}/>
                </button>
            </section>
        </article>
    )
}